let rgbSlider = function (document) {
  const slider = document.querySelector('#rgb-slider');

  slider.insertAdjacentHTML('beforeend','<input type="range" min="0" max="360" value="180" class="rgb-range-slider" />')
  slider.insertAdjacentHTML('beforeend','<div class="rgb-slider-scale"></div>');
  slider.insertAdjacentHTML('beforeend',
    `<div class="rgb-slider-arrow-container flex flex-justify-space-between">
      <div class="rgb-slider-arrow-left"></div>
      <div class="rgb-slider-arrow-right"></div>
    </div>`
  );

  const sliderArrowContainer = document.querySelector('.rgb-slider-arrow-container');
  const sliderArrowRight = document.querySelector('.rgb-slider-arrow-right');
  const sliderArrowLeft = document.querySelector('.rgb-slider-arrow-left');
  const sliderRange = document.querySelector('.rgb-range-slider')

  function fixSlider () {
    const sliderValue = sliderRange.value;
    sliderArrowContainer.style.top = `${(1 - sliderValue/360) * 400 - 8}px`;
    
    const sliderColor = HSLToHex(sliderValue, 100, 50);
  
    sliderArrowRight.style.borderRightColor = sliderColor;
    sliderArrowLeft.style.borderLeftColor = sliderColor;

    return sliderColor;
  }

  return {
    sliderRange,
    fixSlider
  };
}

function HSLToHex(h, s, l) {
  s /= 100;
  l /= 100;

  let c = (1 - Math.abs(2 * l - 1)) * s,
      x = c * (1 - Math.abs((h / 60) % 2 - 1)),
      m = l - c/2,
      r = 0,
      g = 0,
      b = 0;

  if (0 <= h && h < 60) {
    r = c; g = x; b = 0;
  } else if (60 <= h && h < 120) {
    r = x; g = c; b = 0;
  } else if (120 <= h && h < 180) {
    r = 0; g = c; b = x;
  } else if (180 <= h && h < 240) {
    r = 0; g = x; b = c;
  } else if (240 <= h && h < 300) {
    r = x; g = 0; b = c;
  } else if (300 <= h && h <= 360) {
    r = c; g = 0; b = x;
  }
  // Having obtained RGB, convert channels to hex
  r = Math.round((r + m) * 255).toString(16);
  g = Math.round((g + m) * 255).toString(16);
  b = Math.round((b + m) * 255).toString(16);

  // Prepend 0s, if necessary
  if (r.length == 1)
    r = "0" + r;
  if (g.length == 1)
    g = "0" + g;
  if (b.length == 1)
    b = "0" + b;

  return "#" + r + g + b;
}


module.exports = rgbSlider;